import axios from 'axios';
import { Component } from "react";

class GetJsonApi extends Component {

    getApi = async () => { //   async/await  then  catch,     
        const jsonUrl = `${process.env.PUBLIC_URL}/data.json`;
        try {
            const response = await axios(jsonUrl);
            console.log(response)
            return response;
        } catch(error) {
            console.error('   :', error);
            throw error; //  ,     
        }
    }
}

export default GetJsonApi;